.news-page {
  background-color: #fafafa;

  .container {

    h1.is-1 {
      color: #e3120b;
      font-family: 'Bellota Text', sans-serif;
      font-weight: 700;
      padding-bottom: 20px;
      border-bottom: 2px solid #e3120b;
    }
  }

  label {
    font-family: 'Sen', sans-serif;
  }
}

.news-article {
  padding: 15px 10px;
  border-radius: 5px;
  background-color: #fafafa;

  h1, h2, h3 {
    font-family: 'Sen', sans-serif;
    cursor: pointer;
  }

  p {
    font-family: 'Manjari', sans-serif;
    font-weight: 400;
    cursor: pointer;
  }

  hr {
    background-color: #e3120b;
    margin: 15px 0;
  }
}

.news-article:hover {
  border: 2px solid #8abbd0;

  *:not(i) {
    font-style: italic;
  }

  button {
    font-weight: 700;
  }
}

.modal-news-article {
  padding: 15px 10px;
  border-radius: 5px;

  h1, h2, h3 {
    font-family: 'Sen', sans-serif;
  }

  p {
    font-family: 'Manjari', sans-serif;
    font-weight: 400;
  }

  hr {
    background-color: #e3120b;
    margin: 15px 0;
  }
}

i {
  cursor: pointer;
}

i.fa-bookmark {
  color: #e3120b;
}

i.fa-external-link-alt {
  color: cadetblue;
}

div.tabs {
  font-family: 'Bellota Text', sans-serif;

  .is-active {
    font-family: 'Sen', sans-serif;
    font-weight: 700;
  }
}

h2.subtitle.is-3, h3.subtitle.is-5 {
  font-family: 'Bellota Text';
  font-weight: 700;
  font-style: italic;
}